import PostCard from "@/components/blog/PostCard";

const RelatedPosts = ({ postId, posts }) => {
  return (
    <div className="border-t border-black py-8 lg:py-16">
      <div className="text-center font-bold text-xl md:text-2xl lg:text-3xl mb-8 lg:mb-16">
        Related Posts
      </div>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-y-4 lg:gap-y-8 md:gap-8">
        {posts &&
          posts
            .filter((post) => post.id !== postId)
            .map((post) => (
              <PostCard post={post} key={`relatedPost-${post.id}`} />
            ))}
      </div>
    </div>
  );
};
export default RelatedPosts;
