import TextLink from "@/components/ui/TextLink";

const Pager = ({ prev, next }) => {
  return (
    <div className="grid grid-cols-1 lg:grid-cols-2 gap-4 lg:gap-8 pt-8 text-sm lg:text-base">
      {prev ? (
        <TextLink url={`/${prev.id}`}>[이전글] {prev.title}</TextLink>
      ) : (
        <span></span>
      )}
      {next ? (
        <TextLink url={`/${next.id}`}>[다음글] {next.title}</TextLink>
      ) : (
        <span></span>
      )}
    </div>
  );
};
export default Pager;
