import Layout from "@/components/layout/Layout";
import Container from "@/components/ui/Container";
import PostBlock from "@/components/blog/PostBlock";
import Pager from "@/components/blog/Pager";
import PostAuthor from "@/components/blog/PostAuthor";
import Date from "@/components/blog/Date";
import Tag from "@/components/blog/Tag";
import RelatedPosts from "@/components/blog/RelatedPosts";
import GoogleAd from "@/components/ads/GoogleAd";

const BlogPostById = ({ post, blocks, prev, next, relatedPosts }) => {
  const { id, title, tag, tag2, tag3, wdate } = post || {};
  return (
    <Layout>
      <GoogleAd />
      <div className="mx-auto max-w-3xl px-4 py-8 lg:py-16">
        <div className="pb-8 lg:pb-16">
          <h1 className="mb-8 font-extrabold text-2xl lg:text-4xl">{title}</h1>
          <div className="flex-left space-x-4">
            <Tag tag={tag} />
            {tag2 && <Tag tag={tag2} />}
            {tag3 && <Tag tag={tag3} />}
            <Date date={wdate} />
          </div>
        </div>
        <div className="content">
          {blocks &&
            blocks?.map((block) => <PostBlock block={block} key={block.id} />)}
        </div>
        <Pager prev={prev} next={next} />
        <PostAuthor />
      </div>
      <Container>
        <RelatedPosts postId={id} posts={relatedPosts} />
      </Container>
    </Layout>
  );
};
export default BlogPostById;
