import React from "react";
import Image from "next/image";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { materialDark } from "react-syntax-highlighter/dist/cjs/styles/prism";

const ImageBlock = ({ block }) => {
  const { imgData } = block || {};
  const { title, url, extension, alt, width, height } = imgData || {};
  return (
    <div>
      <Image
        src={`${url}.${extension}`}
        alt={alt}
        quality={80}
        width={width}
        height={height}
      />
      {title && (
        <div className="text-center text-xs text-stone-500 mt-4">{title}</div>
      )}
    </div>
  );
};

const CodeBlock = ({ block }) => {
  const { content, option } = block || {};
  return (
    <div className="mt-4 relative">
      <SyntaxHighlighter
        language={option}
        PreTag="div"
        style={materialDark}
        className="rounded-lg overflow-hidden "
      >
        {content}
      </SyntaxHighlighter>
      <div className="text-stone-500 text-xs absolute right-3 top-4">
        {option}
      </div>
    </div>
  );
};

const PostBlock = ({ block }) => {
  const { type, content } = block || {};

  if (type === "code") {
    return <CodeBlock block={block} />;
  }
  if (type === "img") {
    return <ImageBlock block={block} />;
  }
  return React.createElement(type, null, content);
};
export default PostBlock;
